import Vue from 'vue'
import App from './App.vue'

// 引入路由文件
import router from './router/index'
//  element组件
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import './plugins/element.js'
Vue.use(ElementUI);

Vue.config.productionTip = false

new Vue({
  el:'#app',
  router,
  render: h => h(App),
}).$mount('#app')
